import React from 'react';
import { Inners } from '../../styles/Common';
import { Reviews } from './Style';
import ReviewList from '../ReviewList';

const Review = () => {
    return (
        <Reviews>
            <Inners>
                <div className='review-tit'>
                    <h2>
                          <br/>
                           <span className='tit-mark'>.</span>
                    </h2> 
                </div>
                <ReviewList />
            </Inners>
        </Reviews>
    );
};

export default Review;